section {
    position: relative; //just in case we want to absolutely position any children
}

.show {
    display: block !important;
}

.bottom-margin { // optional bottom margin for all blocks
    margin-bottom: 2rem;
}

.half-bottom {
    margin-bottom: 0.5rem !important;
}

.remove-bottom {
    margin-bottom: 0 !important;
}

.small {
    font-size: 85%;
}

.secondary-bg {
    background-color: $secondary-color;
    color: #fff;
}

.absolute-100 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.absolute-cover {
    position: absolute;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    background-size: cover;
    overflow: hidden;
}

.absolute-center {
    @include absolute-center;
}

.has-overlay { // Add a dark overlay to block elements and image
    display: block;
    position: relative;

    &.absolute-100 {
        position: absolute;
    }
}

.has-overlay:after {
    @extend .absolute-100;

    background: $black;
    opacity: 0.3;
    content: "";
}

.tagline {
    color: $dark-gray;
}

// For Block Presentation Only
hr.block-divider {
    max-width: 100%;
    position: relative;
    margin: 5rem auto;
    border-bottom: 1px dashed #cacaca;

    &:after {
        content: "END BLOCK";
        position: absolute;
        left: 0;
        right: 0;
        top: 1px;
        margin: 0 auto;
        width: 100px;
        background: $light-gray;
        text-align: center;
        padding: 0.5rem;
        color: $primary-color;
        font-weight: 600;
        font-size: 0.8rem;
    }
}
