* {
    outline: none;

    &:focus,
    &:before,
    &:after {
        outline: none;
    }
}

[v-cloak] {
    display: none;
}

body {
    padding-top: 50px;
    max-width: 1920px;
    margin: 0 auto;
    -webkit-box-shadow: 1px 2px 10px $border-light-gray;
    box-shadow: 1px 2px 10px $border-light-gray;

    &.large-top-padding {
        padding-top: 130px;
    }

    @include breakpoint(large) {
        padding-top: 70px;

        &.large-top-padding {
            padding-top: 190px;
        }
    }

    @include breakpoint(xlarge) {
        &.large-top-padding {
            padding-top: 220px;
        }
    }
}

a {
    @include transition;
}

.fade-out {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 200ms, opacity 200ms linear;
}

.fade-in {
    visibility: visible;
    opacity: 1;
    transition: opacity 200ms linear;
}

.h1-large {
    font-size: 90px;

    @include breakpoint(medium) {
        font-size: 80px;
    }

    @include breakpoint(large) {
        font-size: 40px;
    }
}

blockquote,
ul li {
    font-size: 14px;

    @include breakpoint(medium) {
        font-size: 15px;
    }

    @include breakpoint(large) {
        font-size: 20px;
    }
}

p {
    font-size: 14px;

    @include breakpoint(large) {
        font-size: 15px;
    }
}

.button {
    font-size: 14px;
    font-weight: bold;
    line-height: 1.2;
    text-transform: uppercase;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    height: 35px;
    margin-bottom: 0;
    padding: 5px 25px 7px;

    &.hollow {
        border-color: $dark-gray;

        &:hover {
            background: #f4f2ef;
            border-color: $border-light-gray;
        }
    }

    &--has-icon {
        position: relative;

        &:before {
            content: '';
            display: inline-block;
            width: 20px;
            height: 20px;
            background-size: 13px;
            background-position: center;
            background-repeat: no-repeat;
            margin-right: 7px;
        }
    }

    &--phone {
        &:before {
            background-image: url("../images/svg/phone.svg");
        }
    }

    &--email {
        &:before {
            background-image: url("../images/svg/email-button.svg");
            background-size: 19px;
        }
    }

    @include breakpoint(large) {
        &--medium {
            height: 50px;
        }

        &--jumbo {
            height: 60px;
        }
    }
}

.button-group {
    display: flex;
    flex-direction: column;

    .button {
        margin-bottom: 15px;

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    .button:last-child {
        margin-bottom: 0;
    }

    @include breakpoint(600px) {
        display: inline-flex;
        flex-direction: row;
        justify-content: center;

        .button {
            margin: 0 10px;
        }
    }
}
