.typography {
    content: "";
    clear: both;
    display: table;
    width: 100%;
}

// Links (.button will not be affected)
.typography a:not(.button) {
    text-decoration: underline;
    color: $black;
    border-bottom: 0;
    box-shadow: none;

    &:hover {
        color: $primary-color;
        background: transparent;
    }
}

.typography .lead {
    @include breakpoint(small down) {
        font-size: 120%;
    }
}

.typography strong {
    font-weight: 600;
}

/* WYSIWYG EDITOR ALIGNMENT CLASSES
-------------------------------------------- */
.typography .left {
    text-align: left;
}

.typography .center {
    text-align: center;
}

.typography .right {
    text-align: right;
}

/* TABLE STYLES
-------------------------------------------- */
.typography table {
    border-collapse: collapse; /* borders are collapsed into a single border when possible */
    border: none;
    border-spacing: 0; /* The border-spacing property sets the distance between the borders of adjacent cells - acts as a backup to border-collapse: collapse */
    margin: 0 0 10px;
    text-align: left;
}

.typography thead,
.typography tbody,
.typography tfoot {
    border: 1px solid $border-gray;
}

.typography table td {
    border: none;
}

.typography table tbody tr {
    border-bottom: 1px solid $border-gray;
    background: transparent;
}

.typography table th {
    font-weight: 600;
    border: none;
    border-bottom: 4px solid $border-gray;
    vertical-align: top;
}

.typography table thead {
    background: none;
}

/* IMAGES
-------------------------------------------- */
.typography img {
    height: auto; /* resets the image height so that it maintains its aspect ratio when width is set */
    // background: transparent url(../images/ajax-loader.gif) no-repeat center center;
}

.typography img.left {
    float: left;
    max-width: 50%;
    margin: 0.5rem 2rem 1rem 0;
}

.typography img.right {
    float: right;
    max-width: 50%; /* Responsive width */
    margin: 0.5rem 0 1rem 2rem;
}

.typography img.leftAlone {
    float: left;
    margin-right: 100%;
    margin-bottom: 1rem;
    clear: both;
}

.typography img.rightAlone {
    float: right;
    margin-left: 100%;
    margin-bottom: 1rem;
    clear: both;
}

.typography img.rightAlone,
.typography img.leftAlone {
    // Image will be full width when screen size is lower than 768px
    @include breakpoint(768px down) {
        width: 100% !important;
    }
}

.typography img.center {
    float: none;
    margin-left: auto;
    margin-right: auto;
    display: block;
    margin-bottom: 1rem;
    clear: both;

    @include breakpoint(large down) {
        width: 100% !important;
        max-width: 600px;
    }
}

.typography .captionImage {
    width: 100%;
    margin-top: 0.5rem;

    @include breakpoint(768px down) {
        width: 100% !important;
    }

    &.rightAlone {
        float: right;
        margin: 0 0 2rem;
        margin-left: 100%;

        @include breakpoint(768px down) {
            margin: 0 auto 1rem !important;
        }
    }
}

// Image with captions will be centered when screen size is lower than 500px
.typography .captionImage.left,
.typography .captionImage.right {
    @include breakpoint(500px down) {
        margin: 0 auto 1rem !important;
        float: none !important;
        width: 100% !important;
        max-width: 100% !important;
    }
}

.typography .captionImage p {
    clear: both;
    margin: 5px 0;
}

.typography .captionImage .caption {
    font-style: italic;
}

.typography .captionImage img {
    margin: 0;

    @include breakpoint(medium down) {
        width: 100%;
    }
}

.typography .captionImage.left {
    float: left;
    margin: 0.5rem 3rem 2rem 0;
}

.typography .captionImage.right {
    float: right;
    margin: 0.5rem 0 2rem 3rem;
}

.typography .captionImage.left[style],
.typography .captionImage.right[style] {
    max-width: 50%; /* Overides core width to make responsive */
}

.typography .captionImage.left img,
.typography .captionImage.right img {
    float: none;
    max-width: none;
    width: 100%;
}

.typography .captionImage.left img {
    margin-right: -1rem;
}

.typography .captionImage.right p {
    text-align: left;
}

.typography .captionImage.leftAlone {
    float: none;
    margin: 0 2rem 2rem 0;
}

.typography .captionImage.center {
    margin: 0 auto 2rem;

    img {
        margin: 0 auto;
    }
}

/* EMBED
-------------------------------------------- */
.typography .embed.center {
    margin: 0 auto 1.5rem;
}

.typography .embed.left {
    float: left;
    margin: 0.5rem 3rem 2rem 0;
}

.typography .embed.right {
    float: right;
    margin: 0.5rem 0 2rem 3rem;
    text-align: left;
}

.typography .embed {
    @include breakpoint(medium down) {
        width: 100% !important;
        margin: 0.5rem auto 2rem !important;
        float: none !important;

        iframe {
            width: 100% !important;
        }
    }
}

/* BLOCKQUOTES
-------------------------------------------- */
.typography blockquote {
    background: transparent url(../images/svg/icon-quote.svg) no-repeat 0 6px;
    background-size: 32px 24px;
    clear: both;
    padding-left: 50px;
    padding-top: 0;
    border: none;
    font-size: 14px;
    line-height: 1.6071;
    color: $black;

    @include breakpoint(large) {
        font-size: 15px;
    }
}

.typography blockquote p {
    color: $black;
    font-size: 14px;
    line-height: 1.6071;

    @include breakpoint(large) {
        font-size: 15px;
    }
}

.typography pre {
    background: #f7f7f7;
    border: 1px solid #e4e4e4;
    font-family: 'brown';
    margin: 0 0 2rem;
    padding: 1.5rem;
    clear: both;
}

/* ADDRESS
-------------------------------------------- */
address {
    display: block;
    margin-bottom: 2rem;
}

/* LISTS
-------------------------------------------- */
.typography ul {
    list-style: none;
    margin-left: 0;

    li:before {
        background: transparent url(../images/svg/icon-disc.svg) no-repeat center center;
        content: "";
        height: 18px;
        width: 18px;
        left: -1.5rem;
        top: 3px;
        position: absolute;
        //background-position: center;
    }

    li {
        margin-left: 1.5rem;
        position: relative;

        @include breakpoint(large) {
            font-size: 15px;
        }
    }
}

.typography ul li,
.typography ol li {
    margin-bottom: 1rem;
}
