.grid-container {
    max-width: $global-width;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.full .grid-container {
    max-width: $full-width;
}

.wide .grid-container {
    max-width: $wide-width;
}

.narrow .grid-container {
    max-width: $narrow-width;
}

//  Optional: If you want a narrower container
//  .grid-container.narrower {
//     max-width: calc($narrow-width/1.2);
//  }

section.block {
    // Default bottom-margin for the block
    margin-bottom: 40px;
    position: relative;
    overflow: hidden;

    // Adding dynamic bottom margin
    @include breakpoint(medium up) {
        margin-bottom: 50px;
    }

    @include breakpoint(large up) {
        margin-bottom: 60px;
    }

    @include breakpoint(xlarge up) {
        margin-bottom: 70px;
    }
}

section.block .empty-block {
    min-height: 80px;
    width: 100%;
    border: 2px dashed $light-gray;
    background: lighten($light-gray, 7%);
    border-radius: $global-radius;
    position: relative;
    margin: 1rem auto;

    p {
        @include absolute-center;

        color: darken($light-gray, 40%);
        margin-bottom: 0;
    }
}

