@font-face {
    font-family: 'GT-Sectra-Display';
    src: url('../fonts/sectra/GT-Sectra-Display-Medium-Web/GT-Sectra-Display-Medium.eot');
    src:
        url('../fonts/sectra/GT-Sectra-Display-Medium-Web/GT-Sectra-Display-Medium.eot?#iefix') format('embedded-opentype'),
        url('../fonts/sectra/GT-Sectra-Display-Medium-Web/GT-Sectra-Display-Medium.ttf') format('truetype'),
        url('../fonts/sectra/GT-Sectra-Display-Medium-Web/GT-Sectra-Display-Medium.woff') format('woff');
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
}

@font-face {
    font-family: 'brown';
    src:
        url('../fonts/new-brown/brown-bold.woff2') format('woff2'),
        url('../fonts/new-brown/brown-bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'brown';
    src:
        url('../fonts/new-brown/brown-regular.woff2') format('woff2'),
        url('../fonts/new-brown/brown-regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.GT-Sectra-Display-web {
    font-family: 'GT-Sectra-Display';
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
}

.Brown-Bold-web {
    font-family: 'brown', 'Open Sans', Helvetica, Arial, sans-serif;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
}
